

// @ts-ignore
const getView = function(page:any){
    return () => import(`@/views/user/${page}.vue`)
}


const userRouter = [
    {
        path: '',
        name: 'home',
        component: getView('Home')
    },
    {
        path: 'farm',
        name: 'farm',
        component: getView('Farm')
    },
    {
        path: 'addfarm',
        name: 'addfarm',
        component: getView('AddFarm')
    },
    {
        path: 'farmdetail',
        name: 'farmdetail',
        component: getView('FarmDetail')
    },
    {
        path: 'cattle',
        name: 'cattle',
        component: getView('Cattle')
    },
    {
        path: 'cattledetail',
        name: 'cattledetail',
        component: getView('CattleDetail')
    },
    {
        path: 'addcattle',
        name: 'addcattle',
        component: getView('AddCattle')
    },
    {
        path: 'guidebook',
        name: 'guidebook',
        component: getView('GuideBook')
    },
]

export   {
    userRouter
}