

// @ts-ignore
const getView = function(page:any){
  return () => import(`@/views/admin/${page}.vue`)
}


const adminRouter = [
  /////////////////// รายงาน //////////////////////
  {
    path: '',
    name: 'home',
    component: getView('Home')
  }, 
  {
      path: 'reportuser',
      name: 'reportuser',
      component: getView('ReportUser')
  }, 
  {
    path: 'reportmap',
    name: 'reportmap',
    component: getView('ReportMap')
  }, 
  {
    path: 'reportcattle',
    name: 'reportcattle',
    component: getView('ReportCattle')
  }, 
  {
    path: 'reportflow',
    name: 'reportflow',
    component: getView('ReportFlow')
  }, 
  {
    path: 'export',
    name: 'export',
    component: getView('Export')
  }, 
  /////////////////// การจัดการผู้ใช้ //////////////////////
  {
    path: 'user',
    name: 'user',
    component: getView('User')
  }, 
  {
    path: 'adduser',
    name: 'adduser',
    component: getView('UserAdd')
  }, 
  {
    path: 'userdetail',
    name: 'userdetail',
    component: getView('UserDetail')
  }, 
  /////////////////// การจัดการผู้ดูแลระบบ //////////////////////
  {
    path: 'admin',
    name: 'admin',
    component: getView('Admin')
  },  
  {
    path: 'addadmin',
    name: 'addadmin',
    component: getView('AdminAdd')
  }, 
  {
    path: 'admindetail',
    name: 'admindetail',
    component: getView('AdminDetail')
  },  
]

export   {
  adminRouter
}